.content
  padding: 30px 0

  &__title
    padding-bottom: 3px
    border-bottom: 1px solid $borderGrey

  &__top
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
    border-bottom: 1px solid $borderGrey

  &__bottom
    padding-top: 20px
    padding-bottom: 100px

  &__left
    width: 72%

  &__right
    width: 28%

  &__wrap
    display: flex
    flex-direction: row
    width: 100%

  &__left
    margin-right: 25px

  &.main
    padding: 40px 0